
export const cpfMask = value => {
    return value
      .replace(/\D/g, '') // substitui qualquer caractere diferente de numrico por vazio
      .replace(/(\d{3})(\d)/, '$1.$2') // captura 3 nmeros e insere ponto aps o terceiro nmero
      .replace(/(\d{3})(\d)/, '$1.$2') // captura 3 nmeros e insere ponto aps o terceiro nmero
      .replace(/(\d{3})(\d{1,2})/, '$1-$2') // captura 3 nmeros e insere hfen aps o terceiro nmero
      .replace(/(-\d{2})\d+?$/, '$1') // captura 2 ltimos numeros
  }

  export const cnpjMask = value => {
    return value
      .replace(/\D/g, '') // substitui qualquer caractere diferente de numrico por vazio
      .replace(/(\d{2})(\d)/, '$1.$2') // captura 2 nmeros e insere ponto aps o segundo nmero
      .replace(/(\d{3})(\d)/, '$1.$2') // captura 3 nmeros e insere ponto aps o terceiro nmero
      .replace(/(\d{3})(\d)/, '$1/$2') // captura 3 nmeros e insere barra aps o terceiro nmero
      .replace(/(\d{4})(\d{1,2})/, '$1-$2') // captura 4 nmeros e insere hfen aps o quarto nmero
      .replace(/(-\d{2})\d+?$/, '$1') // captura 2 ltimos numeros
  }

  export const cellMask = value => {
    return value
      .replace(/\D/g, '')
      .replace(/(\d{2})(\d)/, '($1) $2')
      .replace(/(\d{5})(\d)/, '$1-$2')
      .replace(/(-\d{4})\d+?$/, '$1')
  }

  export const phoneMask = value => {
    return value
      .replace(/\D/g, '')
      .replace(/(\d{2})(\d)/, '($1) $2')
      .replace(/(\d{4})(\d)/, '$1-$2')
      .replace(/(-\d{4})\d+?$/, '$1')
  }

  export const zipMask = value => {
    return value
      .replace(/\D/g, '')
      .replace(/(\d{1})(\d)/, '$1$2')
      .replace(/(\d{5})(\d)/, '$1-$2')
      .replace(/(-\d{3})\d+?$/, '$1')
  }

  export const numMask = value => {
    return value
      .replace(/\D/g, '')
      .replace(/(\d{6})(\d)/, '$1')
      .replace(/(-\d{1})\d+?$/, '$1')
  }

  export const moneyMask = value => {
    if(value.length > 6){
      return value.slice(0,value.indexOf(',')+3)
    } else {
      return value
        .replace(/\D/g, '')
        .replace(/(\d{1})(\d)/, '$1$2')
        .replace(/(\d{1})(\d{1,2})$/,"$1,$2")
    }
  }